@import './main';

a {
  color: $black;
}

.container {
  position: relative;
  background-color: $white;

  color: $black;
  font-size: 24px;

  min-height: calc(100vh - 150px);
  padding-bottom: 150px + 44px;
}

.header {
  padding: $vert-space $side-space;
}

.footer {
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;

  font-size: 16px;
  font-weight: 400;

  & a {
    display: inline-block;
    color: $white;
    text-align: center;
    width: auto;
    min-width: 96px;
    margin: 0 12px;
    padding-top: 8px;
    padding-bottom: 8px;
    border: 0.5px solid $vblack;
    border-radius: 24px;

    &:hover {
      border-color: $white;
      box-shadow: 0 0 0 0.5px $white;
    }
  }
}

.sub-footer-container {
  padding: 36px 24px;
  background-color: $vblack;

  font-size: 14px;
  color: $white;

  & sub-footer-section {

  }
}

.footer-container {
  padding: 16px 24px;
  background-color: $black;

  font-size: 12px;
  color: $deepgrey;
  text-align: center;
}

.nav-logo {
  text-decoration: none;
  color: $black;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 6vh;

  &:hover {
    color: $blue;
  }
}

.nav-link {
  margin: 0;
  padding: 0;
  float: right;

  & a {
    text-decoration: none;
    color: $black;
    padding: 0px 8px;

    &:hover {
      color: $blue;
    }
  }
}

li {
  list-style-type: none;
  display: inline;
  margin: 0;
}

