
/*

  Constants

  - spaces
    - global side space

*/

$side-space: 24px;
$vert-space: 24px;

/*

  Colors

  - colors

*/

/* colors */

$primary-color: deeppink;

$black: #1c1c1e;
$vblack: #303132;
$white: #FFFFFF;
$lightgrey: #d1d1d6;
$stalegrey: #778899;
$grey: #8e8e93;
$deepgrey: #636366;

$blue: #0074D9;
$pblue: #c5e9e7;

/*

  Mixins

  - common

*/

/* common */
@mixin box-shadow($top, $left, $blur, $color, $inset: false) {
  @if $inset {
    -webkit-box-shadow:inset $top $left $blur $color;
    -moz-box-shadow:inset $top $left $blur $color;
    box-shadow:inset $top $left $blur $color;
  } @else {
    -webkit-box-shadow: $top $left $blur $color;
    -moz-box-shadow: $top $left $blur $color;
    box-shadow: $top $left $blur $color;
  }
}

@mixin sector {
  padding: 12px $side-space;
  font-weight: 600;
  line-height: 1.45em;
}

